<template>
	<div id="orientLayer" class="mod-orient-layer">
		<div class="mod-orient-layer__content"><i class="icon mod-orient-layer__icon-orient"></i>
			<div class="mod-orient-layer__desc">为了更好的体验，请使用竖屏浏览</div>
		</div>
	</div>
</template>
<script type="text/javascript">
export default {
	name: 'stoprotate',
	data() {
		return {

		}
	},
	mounted() {
		let $orientLayer = document.querySelector('#orientLayer')
		let $mod_orient_layer = document.querySelector('.mod-orient-layer')

		function oriChange(e) {
			var ori = window.orientation
			if (ori == 180 || ori == 0) {
				$orientLayer.style.display = 'none'
				$mod_orient_layer.style.display = 'none'
			}
			if (ori == 90 || ori == -90) {
				$orientLayer.style.display = 'block'
				$mod_orient_layer.style.display = 'block'
			}
		}
		oriChange()
		window.addEventListener('orientationchange', oriChange.bind(this), false)
	}
}

</script>
<style  lang="scss" scoped>
#orientLayer {
	display: none;
}

.mod-orient-layer {
	display: none;
	position: fixed;
	height: 100%;
	width: 100%;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background: #333;
	z-index: 9997
}

.mod-orient-layer__content {
	position: absolute;
	width: 100%;
	top: 45%;
	margin-top: -75px;
	text-align: center
}

.mod-orient-layer__icon-orient {
	background-repeat: no-repeat;
	background-image: url("data:image/png;base64,");
	display: inline-block;
	width: 67px;
	height: 109px;
	transform: rotate(90deg);
	-webkit-transform: rotate(90deg);
	-webkit-animation: rotation infinite 1.5s ease-in-out;
	animation: rotation infinite 1.5s ease-in-out;
	-webkit-background-size: 67px;
	background-size: 67px
}

.mod-orient-layer__desc {
	margin-top: 20px;
	font-size: 15px;
	color: #fff
}

@-webkit-keyframes rotation {
	10% {
		transform: rotate(90deg);
		-webkit-transform: rotate(90deg)
	}
	50%,
	60% {
		transform: rotate(0deg);
		-webkit-transform: rotate(0deg)
	}
	90% {
		transform: rotate(90deg);
		-webkit-transform: rotate(90deg)
	}
	100% {
		transform: rotate(90deg);
		-webkit-transform: rotate(90deg)
	}
}

@keyframes rotation {
	10% {
		transform: rotate(90deg);
		-webkit-transform: rotate(90deg)
	}
	50%,
	60% {
		transform: rotate(0deg);
		-webkit-transform: rotate(0deg)
	}
	90% {
		transform: rotate(90deg);
		-webkit-transform: rotate(90deg)
	}
	100% {
		transform: rotate(90deg);
		-webkit-transform: rotate(90deg)
	}
}

</style>



